.SVGSteps {
    -webkit-animation: SVGStep 6s linear;
            animation: SVGStep 6s linear;
}

@-webkit-keyframes SVGStep {
    0%        {fill: #FC4F1E}
    48%        {fill: #FC4F1E}
  52%       {fill: #FFFFFF}
  100% {fill: #FFFFFF}

}

@keyframes SVGStep {
    0%        {fill: #FC4F1E}
    48%        {fill: #FC4F1E}
  52%       {fill: #FFFFFF}
  100% {fill: #FFFFFF}

}


