.Slider {
    background-color: #2CFEE6;
    color: #fff;
    position: absolute;
    width: 100%;
    height: 100%;
    font: 28px/1em 'Helvetica';
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    overflow: hidden;
  }
  
  
  .joystick {

    -ms-flex-item-align: center;

        align-self: center;
    justify-self: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    z-index: 500;

    
  }
/*   @media (max-width: 700px) {
    .joystick {
      transform: scale(0.5)
    }
      
    };
   */
  .triangle {
    -ms-flex-item-align: center;
        align-self: center;
    justify-self: center;
    position: absolute;
  }

  .piste {
    width: 100%;
    height: 100px;
    border-top : 4px solid #FC4F1E;
    border-bottom : 4px solid #FC4F1E;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, rgba(255, 192, 203, 0)), color-stop(50%, rgba(255, 255, 255, 0.3)));
    background: -webkit-linear-gradient(left, rgba(255, 192, 203, 0) 50%, rgba(255, 255, 255, 0.3) 50%);
    background: -o-linear-gradient(left, rgba(255, 192, 203, 0) 50%, rgba(255, 255, 255, 0.3) 50%);
    background: linear-gradient(90deg, rgba(255, 192, 203, 0) 50%, rgba(255, 255, 255, 0.3) 50%);

  }


